import BaseFormDialog from './BaseFormDialog';
export default BaseFormDialog.extend({
  save: 'OK', // eslint-disable-line @okta/okta/no-unlocalized-text

  params: {
    minWidth: 500,
    maxWidth: 700,
    close: true
  },

  constructor: function () {
    BaseFormDialog.apply(this, arguments);

    if (this.content) {
      this.add(this.content);
    }

    this.listenTo(this, 'save', function () {
      const callback = this.ok || this.options.ok;

      callback && callback();
      this.remove();
    });

    this.listenTo(this, 'cancel', function () {
      const callback = this.cancelFn || this.options.cancelFn;

      callback && callback();
    });
  }
});
